<template>
  <div class="settings-page">
    <v-snackbar
        top
        color="teal lighten-1 white--text"
        timeout="1600"
        light
        v-model="snack">
      {{snackText}}
    </v-snackbar>
    <v-card
        class="teal lighten-5 settings-card"
        elevation="10"
      >
      <v-container>
        <v-row>
          <v-col class="title1 teal--text">
              用 户
          </v-col>
        </v-row>
        <v-row no-gutters dense>
          <v-col align="center">
            <v-avatar
                size="100px"
                class="teal lighten-4"
            >
              <img
                  alt="Avatar"
                  :loading="loading"
                  :src="GLOBAL.baseURL + GLOBAL.userInfo.portrait"
              >
            </v-avatar>
          </v-col>

          <v-col align="left">
            <p class="nickname">{{GLOBAL.userInfo.nickname}}</p>
            <p class="userid">ID: {{GLOBAL.userInfo.id}}</p>
          </v-col>
        </v-row>
        <v-row dense class="settings-row">
          <v-col align="center" class="title2" cols="7">
            麦克风默认开启
          </v-col>
          <v-col align="center">
            <v-switch
                v-model="microphone"
                label=""
                color="teal"
                hide-details
            ></v-switch>
          </v-col>
        </v-row>
        <v-row dense class="settings-row">
          <v-col align="center" class="title2" cols="7">
            摄像头默认开启
          </v-col>
          <v-col align="center">
            <v-switch
                v-model="camera"
                label=""
                color="teal"
                hide-details
            ></v-switch>
          </v-col>
        </v-row>
        <v-row dense>
          <v-col align="center">
            <v-btn
                color="red accent-4 white--text"
                class="mr-4"
                @click="logout"
                :loading="loading"
                :disabled="loading"
                large
            >
              退出登录
            </v-btn>
          </v-col>
          <v-col align="center">
            <v-btn
                class="mr-4 teal white--text"
                @click="back"
                :loading="loading"
                :disabled="loading"
                large
            >
              会 议 >
            </v-btn>
          </v-col>
        </v-row>
      </v-container>
    </v-card>
  </div>
</template>

<script>
import axios from "axios";
const FormData = require("form-data");
const fs = require('fs');

export default {
  name: "Settings",
  components: {
  },
  data(){
    return{
      email : "",
      password : "",
      loading: false,
      file: null,
      microphone : false,
      camera : false,
      snack : false,
      snackText : "",
    }
  },
  methods:{
    logout(){
      this.$emit('logout');
    },
    back(){
      this.GLOBAL.openMicrophoneWhenEnter = this.microphone;
      this.GLOBAL.openCameraWhenEnter = this.camera;
      this.$emit('back');
    },
    getPortrait(){

    },
    async uploadPortrait(){
      this.loading = true;
      this.file = fs.createReadStream('local/path/image.jpg');
      const formData = new FormData();
      formData.append('file', this.file);
      console.log(this.file);
      try{
        const response =await axios(
            {
              method : 'post',
              url : 'http://se-summer.cn:4446/portrait?token='+this.GLOBAL.userInfo.token,
              headers:{'Content-Type': 'multipart/form-data'},
              data : formData
            })
        this.GLOBAL.userInfo.portrait = response.data.filename;
        this.snackText = '上传成功';
        this.snack = true;
        this.loading = false;
        console.log(response.data.filename);
      }catch(error){
        if (error.response){
          this.snackText = error.response.data.error;
        }
        else{
          this.snackText = '无法连接至服务器'
        }
        this.snack = true;
        this.loading = false;
      }
    }
  },
}
</script>

<style scoped>
.title1{
  font-size: 40px;
  font-weight: bold;
  text-align: center;
  margin-bottom: 20px;
}
.title2{
  font-size: 22px;
  text-align: center;
  margin: 16px;
}
.settings-card{
  //margin-top: 20%;
  padding: 3% 3%;
  //width: 60%;
  margin: 10% 10%;
}
.userid{
  font-family: "Microsoft YaHei UI", serif;
  font-size: 16px;
  text-align: left;
  margin-top: 5px;
}
.nickname{
  font-family: "Microsoft YaHei UI", serif;
  font-size: 40px;
  text-align: left;
  margin-bottom: 10px;
}
.file-input{
  cursor: default;
  position: absolute;
  opacity: 0;
  top: 0;
  left: 0;
}
.file-input:hover{
  cursor: pointer;
}
.settings-row{
  margin: 0;
  margin-bottom: 20px;
  height: 50px;
}
</style>