<template>
  <v-list-item-content>
  <v-hover v-slot="{hover}">
  <v-card
      height="150px"
      outlined
      elevation="13">
    <video :id="'sub-video-window'+user.id" autoplay>
    </video>
    <template>
      <v-expand-transition>
        <div
            v-if="hover"
            class="d-flex transition-fast-in-fast-out white black--text v-card--reveal"
            style="height: 20%;">
          <p id="rightSideBarText">
            {{user.displayName}}
          </p>
          <v-spacer></v-spacer>
          <v-btn icon @click="$emit('sub2main')">
            <v-icon color="blue lighten-2">
              mdi-account-star
            </v-icon>
          </v-btn>
          <v-btn icon @click="$emit('removesubfollowuser')">
            <v-icon color="yellow darken-3">
              mdi-close
            </v-icon>
          </v-btn>
        </div>
      </v-expand-transition>
    </template>
  </v-card>
  </v-hover>
  </v-list-item-content>
</template>

<script>
export default {
  name: "subVideo",

  props:['user'],



  mounted() {
    document.getElementById('sub-video-window'+this.user.id).srcObject = this.user.mediaStream;
  }
}
</script>

